import { useLocation, useNavigate } from "react-router-dom";
import { useRef, useState, useEffect } from "react";
import axios from "axios";
import { FaTrash, FaDownload, FaEye, FaTimes } from "react-icons/fa";

const ReportUploadPageLayer = () => {
  const fileInputRef = useRef(null);
  const { state } = useLocation();
  const navigate = useNavigate();

  const { service, booking, userId } = state || {};

  console.log("Service", service.bookingId);

  const [files, setFiles] = useState([]);
  const [notes, setNotes] = useState("");
  const [loading, setLoading] = useState(false);
  const [existingReports, setExistingReports] = useState([]);
  const [reportsLoading, setReportsLoading] = useState(true);
  
  // Modal states
  const [showFilesModal, setShowFilesModal] = useState(false);
  const [selectedReportFiles, setSelectedReportFiles] = useState(null);
  console.log("selectedRepor",selectedReportFiles);
  
  const [downloading, setDownloading] = useState({});

  // Pagination states
  const [currentPage, setCurrentPage] = useState(1);
  const [reportsPerPage, setReportsPerPage] = useState(10);

  const modalRef = useRef(null);
    
  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showFilesModal
      ) {
        setShowFilesModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // üîë Scroll lock logic
    if (showFilesModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [showFilesModal]);

  // Fetch existing reports when component loads
  useEffect(() => {
    const fetchExistingReports = async () => {
      console.log("calling");
      
      if (!service?.bookingId) return;
      
      try {
        setReportsLoading(true);
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/reports/booking/${service.bookingId}`
        );
        console.log("res", response.data);
        
        setExistingReports(response.data || []);
      } catch (err) {
        console.error("Failed to fetch reports:", err);
        setExistingReports([]);
      } finally {
        setReportsLoading(false);
      }
    };

    fetchExistingReports();
  }, [service?.bookingId]);

  // Reset to page 1 when existingReports changes
  useEffect(() => {
    setCurrentPage(1);
  }, [existingReports]);

  // Move the conditional return AFTER all hooks
  if (!service) return <p>No service data provided</p>;

  const allowedTypes = [
    "image/jpeg",
    "image/jpg",
    "image/png",
    "application/pdf",
    "application/msword",
    "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
    "text/plain",
  ];

  const MAX_FILE_SIZE = 5 * 1024 * 1024;

  // Pagination logic
  const indexOfLastReport = currentPage * reportsPerPage;
  const indexOfFirstReport = indexOfLastReport - reportsPerPage;
  const currentReports = existingReports.slice(indexOfFirstReport, indexOfLastReport);
  const totalPages = Math.ceil(existingReports.length / reportsPerPage);

  const paginate = (pageNumber) => {
    if (pageNumber >= 1 && pageNumber <= totalPages) {
      setCurrentPage(pageNumber);
    }
  };

  const goToNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  const goToPrevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleItemsPerPageChange = (e) => {
    setReportsPerPage(Number(e.target.value));
    setCurrentPage(1); // Reset to first page
  };

  const handleFileChange = (e) => {
    const selectedFiles = Array.from(e.target.files);

    if (files.length + selectedFiles.length > 10) {
      alert("You can upload only 10 files in total.");
      if (fileInputRef.current) fileInputRef.current.value = "";
      return;
    }

    const validFiles = [];
    const invalidFiles = [];

    selectedFiles.forEach((file) => {
      if (!allowedTypes.includes(file.type)) {
        invalidFiles.push(`${file.name} (invalid type)`);
      } else if (file.size > MAX_FILE_SIZE) {
        invalidFiles.push(`${file.name} (too large > 5MB)`);
      } else {
        validFiles.push(file);
      }
    });

    if (invalidFiles.length > 0) {
      alert(
        `Some files were rejected:\n${invalidFiles.join(
          "\n"
        )}\n\nAllowed types: JPG, JPEG, PNG, PDF, DOC, DOCX, TXT. Max size: 5MB.`
      );
    }

    setFiles((prev) => [...prev, ...validFiles]);

    if (fileInputRef.current) fileInputRef.current.value = "";
  };

  const handleRemoveFile = (index) => {
    setFiles((prev) => prev.filter((_, i) => i !== index));
  };

  const handleUpload = async () => {
    if (!files.length && !notes.trim()) {
      alert("Please provide at least one file or enter notes before uploading.");
      return;
    }

    setLoading(true);
    const formData = new FormData();
    formData.append("bookingId", booking._id);
    formData.append("serviceBookingId", service.bookingId);
    formData.append("userId", userId);
    formData.append("centreId", service?.centre?._id);
    formData.append("notes", notes);

    files.forEach((f) => formData.append("file", f));

    try {
      await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/reports/upload`,
        formData,
        { headers: { "Content-Type": "multipart/form-data" } }
      );

      alert("Report uploaded successfully!");

      // Refresh the existing reports list
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/reports/booking/${service.bookingId}`
      );
      setExistingReports(response.data || []);

      // Reset form
      setFiles([]);
      setNotes("");
      if (fileInputRef.current) fileInputRef.current.value = "";
    } catch (err) {
      console.error("Upload failed:", err);
      alert(err.response?.data?.message || "Upload failed");
    } finally {
      setLoading(false);
    }
  };

  const handleDownload = async (fileUrl, fileName, fileId) => {
    try {
      setDownloading((prev) => ({ ...prev, [fileId]: true }));

      const response = await fetch(fileUrl.replace("http://", "https://"));
      const blob = await response.blob();

      // Create a temporary URL for the blob
      const url = window.URL.createObjectURL(blob);

      // Create a temporary anchor element and trigger download
      const link = document.createElement("a");
      link.href = url;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();

      // Cleanup
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);
    } catch (error) {
      console.error("Download failed:", error);
      alert("Download failed. Please try again.");
    } finally {
      setDownloading((prev) => ({ ...prev, [fileId]: false }));
    }
  };

  const handleView = (fileUrl) => {
    window.open(fileUrl, '_blank');
  };

  const openFilesModal = (report) => {
    setSelectedReportFiles(report);
    setShowFilesModal(true);
  };

  const closeFilesModal = () => {
    setShowFilesModal(false);
    setSelectedReportFiles(null);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div className="reports-container">
      {/* Upload Section */}
      <div className="card shadow-sm p-4 mb-4">
        <h3 className="mb-3">Upload New Report</h3>

        {/* Booking & Service Info */}
        <div className="mb-4">
          <p>
            <strong>Booking ID:</strong> {service.bookingId}
          </p>
          <p>
            <strong>Service Name:</strong> {service.name}
          </p>
          <p>
            <strong>Appointment Date:</strong> {service.appointmentDate}
          </p>
          <p>
            <strong>Appointment Time:</strong> {service.appointmentTime}
          </p>
        </div>

        {/* File Upload */}
        <div className="mb-3">
          <label className="form-label">Upload Files</label>
          <input
            type="file"
            multiple
            className="form-control"
            accept=".jpg,.jpeg,.png,.pdf,.doc,.docx,.txt"
            onChange={handleFileChange}
            ref={fileInputRef} 
          />

          {/* File Preview List */}
          {files.length > 0 && (
            <ul className="list-group mt-3">
              {files.map((file, index) => (
                <li
                  key={index}
                  className="list-group-item d-flex justify-content-between align-items-center"
                >
                  <div>
                    <strong>{file.name}</strong>{" "}
                    <small className="text-muted">
                      ({(file.size / 1024 / 1024).toFixed(2)} MB)
                    </small>
                  </div>

                  <button
                    type="button"
                    className="btn btn-sm btn-outline-danger d-flex align-items-center"
                    onClick={() => handleRemoveFile(index)}
                  >
                    <span className="d-none d-md-inline">Remove</span>
                    <FaTrash className="d-inline d-md-none" />
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>

        {/* Notes */}
        <div className="mb-3">
          <label className="form-label">Notes</label>
          <textarea
            className="form-control"
            placeholder="Add notes"
            value={notes}
            onChange={(e) => setNotes(e.target.value)}
            rows="4"
          />
        </div>

        {/* Buttons */}
        <div className="d-flex gap-2">
          <button
            className="btn btn-success"
            onClick={handleUpload}
            disabled={loading}
          >
            {loading ? "Uploading..." : "Upload Report"}
          </button>
          <button className="btn btn-secondary" onClick={() => navigate(-1)}>
            Back
          </button>
        </div>
      </div>

      {/* Existing Reports Section */}
      <div className="header-section">
        <h1 className="page-title">üìë Uploaded Reports</h1>
        <div className="reports-count">
          {existingReports.length > 0 && (
            <span className="count-badge">
              {existingReports.length} Reports Found {totalPages > 1 && `| Page ${currentPage} of ${totalPages}`}
            </span>
          )}
        </div>
      </div>

      {reportsLoading ? (
        <div className="text-center">
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
          <p className="mt-2">Loading reports...</p>
        </div>
      ) : existingReports.length === 0 ? (
        <div className="no-data-wrapper">
          <div className="no-data-icon">üìã</div>
          <p className="no-data">No reports available at the moment.</p>
          <p className="no-data-subtitle">
            Reports will appear here once they are uploaded.
          </p>
        </div>
      ) : (
        <>
          {/* Desktop Table View */}
          <div className="table-wrapper desktop-view">
            <table className="reports-table">
              <thead>
                <tr>
                  <th style={{ width: '20%' }}>Service / Package</th>
                  <th style={{ width: '12%' }}>Appointment Date</th>
                  <th style={{ width: '12%' }}>Appointment Time</th>
                  <th style={{ width: '12%' }}>Files</th>
                  <th style={{ width: '16%' }}>Notes</th>
                  <th style={{ width: '15%' }}>Uploaded At</th>
                  <th style={{ width: '13%' }}>Actions</th>
                </tr>
              </thead>
              <tbody>
                {currentReports.map((report) => (
                  <tr key={report._id} className="report-row">
                    <td data-label="Service" style={{ width: '20%' }}>{report.serviceName}</td>
                    <td data-label="Date" style={{ width: '12%' }}>
                      {new Date(report.appointmentDate).toLocaleDateString()}
                    </td>
                    <td data-label="Time" style={{ width: '12%' }}>{report.appointmentTime}</td>
                    <td data-label="Files" style={{ width: '12%' }}>
                      <span style={{ whiteSpace: 'nowrap' }}>
                        üìÅ {report.files?.length || 0} files
                      </span>
                    </td>
                    <td data-label="Notes" style={{ width: '16%', maxWidth: '200px' }}>
                      <div style={{ 
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap'
                      }}>
                        {report.notes ? (
                          <span title={report.notes}>
                            {report.notes.length > 30 
                              ? `${report.notes.substring(0, 30)}...` 
                              : report.notes
                            }
                          </span>
                        ) : (
                          <span className="text-muted">No notes</span>
                        )}
                      </div>
                    </td>
                    <td data-label="Uploaded" style={{ width: '15%' }}>
                      {formatDate(report.createdAt || report.uploadDate)}
                    </td>
                    <td data-label="Actions" style={{ width: '13%' }}>
                      <button
                        className="view-details-btn"
                        onClick={() => openFilesModal(report)}
                        style={{
                          whiteSpace: 'nowrap',
                          padding: '4px 12px',
                          backgroundColor: '#007bff',
                          color: 'white',
                          border: 'none',
                          borderRadius: '4px',
                          cursor: 'pointer',
                          fontSize: '14px'
                        }}
                        title="View report details"
                      >
                        View Details
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Mobile Card View */}
          <div className="mobile-view">
            {currentReports.map((report) => (
              <div key={report._id} className="report-card">
                <div className="card-header">
                  <h3 className="card-title">{report.serviceName}</h3>
                  <span className={`badge ${report.type}`}>{report.type}</span>
                </div>
                <div className="card-body">
                  <div className="card-field">
                    <span className="field-label">Date:</span>
                    <span className="field-value">
                      {new Date(report.appointmentDate).toLocaleDateString()}
                    </span>
                  </div>
                  <div className="card-field">
                    <span className="field-label">Time:</span>
                    <span className="field-value">
                      {report.appointmentTime}
                    </span>
                  </div>
                  <div className="card-field">
                    <span className="field-label">Files:</span>
                    <span className="field-value">
                      üìÅ {report.files?.length || 0} files
                    </span>
                  </div>
                  {report.notes && (
                    <div className="card-field notes-field">
                      <span className="field-label">Notes:</span>
                      <span 
                        className="field-value"
                        style={{
                          display: '-webkit-box',
                          WebkitLineClamp: 3,
                          WebkitBoxOrient: 'vertical',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                          lineHeight: '1.4',
                          maxHeight: '4.2em'
                        }}
                        title={report.notes}
                      >
                        {report.notes}
                      </span>
                    </div>
                  )}
                  <div className="card-field">
                    <span className="field-label">Uploaded:</span>
                    <span className="field-value">
                      {formatDate(report.createdAt || report.uploadDate)}
                    </span>
                  </div>
                  <div className="card-actions">
                    <button
                      className="mobile-view-details-btn"
                      onClick={() => openFilesModal(report)}
                      style={{
                        width: '100%',
                        padding: '10px',
                        backgroundColor: '#007bff',
                        color: 'white',
                        border: 'none',
                        borderRadius: '6px',
                        cursor: 'pointer',
                        fontSize: '14px',
                        fontWeight: '500',
                        marginTop: '10px'
                      }}
                    >
                      View Details
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Pagination Controls */}
          {totalPages > 1 && (
            <div className="pagination-wrapper">
              <div className="pagination-top">
                <div className="pagination-info">
                  Showing {indexOfFirstReport + 1} to {Math.min(indexOfLastReport, existingReports.length)} of {existingReports.length} reports
                </div>
                
                {/* <div className="pagination-per-page">
                  <label htmlFor="itemsPerPage">Show: </label>
                  <select
                    id="itemsPerPage"
                    value={reportsPerPage}
                    onChange={handleItemsPerPageChange}
                    className="per-page-select"
                  >
                    <option value={5}>5</option>
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                    <option value={50}>50</option>
                  </select>
                  <span>per page</span>
                </div> */}
              </div>

              <div className="pagination-controls">
                <button 
                  className="pagination-btn prev-btn" 
                  onClick={goToPrevPage}
                  disabled={currentPage === 1}
                >
                  ‚Üê Previous
                </button>
                
                <div className="pagination-numbers">
                  {/* Always show first page */}
                  {currentPage > 3 && (
                    <>
                      <button
                        className="pagination-btn page-btn"
                        onClick={() => paginate(1)}
                      >
                        1
                      </button>
                      {currentPage > 4 && <span className="pagination-ellipsis">...</span>}
                    </>
                  )}

                  {/* Show pages around current page */}
                  {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                    let pageNum;
                    if (totalPages <= 5) {
                      pageNum = i + 1;
                    } else if (currentPage <= 3) {
                      pageNum = i + 1;
                    } else if (currentPage >= totalPages - 2) {
                      pageNum = totalPages - 4 + i;
                    } else {
                      pageNum = currentPage - 2 + i;
                    }
                    
                    if (pageNum < 1 || pageNum > totalPages) return null;
                    
                    return (
                      <button
                        key={pageNum}
                        className={`pagination-btn page-btn ${
                          currentPage === pageNum ? 'active' : ''
                        }`}
                        onClick={() => paginate(pageNum)}
                      >
                        {pageNum}
                      </button>
                    );
                  })}

                  {/* Always show last page */}
                  {currentPage < totalPages - 2 && (
                    <>
                      {currentPage < totalPages - 3 && <span className="pagination-ellipsis">...</span>}
                      <button
                        className="pagination-btn page-btn"
                        onClick={() => paginate(totalPages)}
                      >
                        {totalPages}
                      </button>
                    </>
                  )}
                </div>
                
                <button 
                  className="pagination-btn next-btn" 
                  onClick={goToNextPage}
                  disabled={currentPage === totalPages}
                >
                  Next ‚Üí
                </button>
              </div>
            </div>
          )}
        </>
      )}

      {/* Files Modal */}
      {showFilesModal && selectedReportFiles && (
        <div className="files-modal-overlay" onClick={closeFilesModal}>
          <div className="files-modal" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3 className="modal-title">
                üìÅ Reports
              </h3>
              <button className="modal-close" onClick={closeFilesModal}>
                <FaTimes />
              </button>
            </div>

            <div className="modal-info">
              <div className="modal-info-row">
                <span className="info-label">Service:</span>
                <span className="info-value">
                  {selectedReportFiles.serviceName}
                </span>
              </div>
              <div className="modal-info-row">
                <span className="info-label">Date:</span>
                <span className="info-value">
                  {new Date(
                    selectedReportFiles.appointmentDate
                  ).toLocaleDateString()}
                </span>
              </div>
            
              <div className="modal-info-row" style={{alignItems: 'flex-start'}}>
                <span className="info-label" style={{minWidth: '80px'}}>Notes:</span>
                <div style={{flex: 1, minWidth: 0}}>
                  {selectedReportFiles.notes ? (
                    <div 
                      style={{
                        whiteSpace: 'pre-wrap',
                        wordWrap: 'break-word',
                        lineHeight: '1.5',
                        padding: '8px 12px',
                        background: '#f8f9fa',
                        borderRadius: '6px',
                        border: '1px solid #e9ecef',
                        maxHeight: '150px',
                        overflowY: 'auto',
                        fontSize: '14px'
                      }}
                    >
                      {selectedReportFiles.notes}
                    </div>
                  ) : (
                    <span 
                      style={{
                        color: '#6c757d',
                        fontStyle: 'italic',
                        padding: '8px 12px',
                        background: '#f8f9fa',
                        borderRadius: '6px',
                        border: '1px dashed #dee2e6',
                        display: 'inline-block'
                      }}
                    >
                      No notes provided
                    </span>
                  )}
                </div>
              </div>
            </div>

            <div className="modal-body">
              <div className="files-header">
                <h4>üìÑ Documents ({selectedReportFiles.files?.length || 0})</h4>
              </div>

              <div className="modal-files-list">
                {selectedReportFiles.files?.map((file, index) => (
                  <div key={file._id || index} className="modal-file-item">
                    <div className="file-number">
                      {String(index + 1).padStart(2, "0")}
                    </div>

                    <div className="modal-file-info">
                      <div className="modal-file-name">
                        <span className="file-icon">üìÑ</span>
                        <span className="file-title" title={file.fileName}>
                          {file.fileName}
                        </span>
                      </div>
                      <div className="modal-file-actions">
                        <a
                          href={file.fileUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="modal-view-btn"
                          title="View in new tab"
                        >
                          üëÅÔ∏è View
                        </a>
                        <button
                          onClick={() =>
                            handleDownload(
                              file.fileUrl,
                              file.fileName,
                              file._id || index
                            )
                          }
                          className="modal-download-btn"
                          disabled={downloading[file._id || index]}
                          title="Download to your computer"
                        >
                          {downloading[file._id || index] ? (
                            <>
                              <span className="download-spinner"></span>
                              Downloading...
                            </>
                          ) : (
                            <>‚¨áÔ∏è Download</>
                          )}
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ReportUploadPageLayer;